.scroll {
  width: 42px;
  height: 42px;
  border: 2px solid rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 90%;
  right: 50%;
}
.scroll__img {
  color: rgb(255, 255, 255);
}
.scroll:hover, .scroll:focus {
  cursor: pointer;
  border-color: rgba(240, 20, 90, 0.5);
  transition: all 0.5s;
}
.scroll:hover .scroll__img, .scroll:focus .scroll__img {
  color: rgb(240, 20, 90);
  transition: all 0.5s;
}

.wrapper {
  background: linear-gradient(228.57deg, #323264 11.93%, #323264 57.17%, #643e72 87.46%);
  font-weight: 400;
  line-height: 20px;
  color: rgb(255, 255, 255);
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}

.wrap-header-hero {
