.auth-nav {
  display: flex;
  align-items: center;
  gap: 18px;
  margin-bottom: 16px;
}
@media screen and (max-width: 768px) {
  .auth-nav {
    margin-bottom: 30px;
  }
}
.auth-nav__social:hover .facebook-svg, .auth-nav__social:focus .facebook-svg {
  color: rgb(240, 20, 90);
  transition: all 0.5s;
}
.auth-nav__list {
  display: flex;
}
.auth-nav__address {
  position: relative;
}
.auth-nav__address::before {
  content: "";
  position: absolute;
  left: -11px;
  bottom: 7px;
  background: rgb(230, 230, 240);
  width: 6px;
  height: 6px;
  border-radius: 50%;
}
.auth-nav__tel {
  font-size: 16px;
  font-weight: 700;
  position: relative;
}
.auth-nav__tel::before {
  content: "";
  position: absolute;
  left: -11px;
  bottom: 7px;
  background: rgb(230, 230, 240);
  width: 6px;
  height: 6px;
  border-radius: 50%;
}
@media screen and (max-width: 376px) {
  .auth-nav__tel::before {
    display: none;
  }
}
.auth-nav__tel::after {
  content: "";
  position: absolute;
  right: -11px;
  bottom: 7px;
  background: rgb(230, 230, 240);
  width: 6px;
  height: 6px;
  border-radius: 50%;
}
@media screen and (max-width: 376px) {
  .auth-nav__tel::after {
    display: none;
  }
}

