.footer {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  background: rgb(245, 245, 250);
  padding: 30px 160px;
}
@media screen and (max-width: 1440px) {
  .footer {
    max-width: 1440px;
    padding: 30px 60px;
  }
}
@media screen and (max-width: 1024px) {
  .footer {
    max-width: 1024px;
  }
}
@media screen and (max-width: 768px) {
  .footer {
    max-width: 768px;
  }
}
@media screen and (max-width: 376px) {
  .footer {
    max-width: 375px;
    padding: 30px;
  }
}
.footer__line {
  height: 2px;
  background: rgb(230, 230, 240);
  margin-bottom: 30px;
}
.footer__wrap {
  display: flex;
  justify-content: space-between;
}
@media screen and (max-width: 768px) {
  .footer__wrap {
    flex-wrap: wrap;
    justify-content: center;
  }
}
.footer__location {
  display: flex;
  align-items: center;
  gap: 26px;
  margin-bottom: 21px;
}
@media screen and (max-width: 764px) {
  .footer__location {
    justify-content: center;
  }
}
@media screen and (max-width: 404px) {
  .footer__location {
    flex-direction: column;
    gap: 10px;
  }
}
.footer__address {
  color: rgb(50, 50, 100);
  line-height: 20px;
}
@media screen and (min-width: 405px) {
  .footer__address {
    white-space: nowrap;
  }
}
.footer__map {
  position: relative;
  text-transform: uppercase;
  color: rgb(36, 163, 255);
  white-space: nowrap;
}
.footer__map::before {
  content: "";
  position: absolute;
  left: -16px;
  bottom: 5px;
  background: rgb(179, 179, 186);
  width: 6px;
  height: 6px;
  border-radius: 50%;
}
@media screen and (max-width: 404px) {
  .footer__map::before {
    display: none;
  }
}
.footer__contacts {
  position: relative;
  display: flex;
  align-items: center;
  gap: 26px;
}
@media screen and (max-width: 764px) {
  .footer__contacts {
    margin-bottom: 30px;
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media screen and (max-width: 376px) {
  .footer__contacts {
    flex-direction: column;
    gap: 10px;
  }
}
.footer__social:hover .footer__facebook, .footer__social:focus .footer__facebook {
  color: rgb(240, 20, 90);
  transition: all 0.5s;
}
.footer__facebook {
  color: rgb(36, 163, 255);
}
.footer__tel {
  font-weight: 700;
  line-height: 20px;
  white-space: nowrap;
  color: rgb(70, 70, 110);
}
.footer__tel::before {
  content: "";
  position: absolute;
  left: 34px;
  bottom: 11px;
  background: rgb(179, 179, 186);
  width: 6px;
  height: 6px;
  border-radius: 50%;
}
@media screen and (max-width: 504px) {
  .footer__tel::before {
    display: none;
  }
}
.footer__mail {
  font-weight: 700;
  line-height: 20px;
  color: rgb(36, 163, 255);
}
.footer__mail::before {
  content: "";
  position: absolute;
  left: 158px;
  bottom: 11px;
  background: rgb(179, 179, 186);
  width: 6px;
  height: 6px;
  border-radius: 50%;
}
@media screen and (max-width: 504px) {
  .footer__mail::before {
    display: none;
  }
}
.footer__mail:hover, .footer__mail:focus {
  color: rgb(240, 20, 90);
  transition: all 0.5s;
}
.footer__copyright {
  line-height: 20px;
  color: rgb(179, 179, 186);
}
@media screen and (max-width: 376px) {
  .footer__copyright {
    text-align: center;
  }
}

.modal {
  position: absolute;
  top: 12%;
  right: 31%;
  width: 100%;
  max-width: 650px;
  padding: 30px;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  font-family: Montserrat;
  background: rgb(245, 245, 250);
  box-shadow: 0px 15px 80px 0px rgba(40, 40, 86, 0.5);
}
@media screen and (max-width: 1440px) {
  .modal {
    right: 24%;
  }
}
@media screen and (max-width: 1150px) {
  .modal {
    right: 19%;
  }
}
@media screen and (max-width: 950px) {
  .modal {
    right: 14%;
  }
}
@media screen and (max-width: 850px) {
  .modal {
    right: 11%;
  }
}
@media screen and (max-width: 768px) {
  .modal {
    right: 7%;
  }
}
@media screen and (max-width: 720px) {
  .modal {
    right: 4%;
  }
}
@media screen and (max-width: 700px) {
  .modal {
    right: 2%;
  }
}
@media screen and (max-width: 670px) {
  .modal {
    right: 1%;
  }
}
@media screen and (max-width: 655px) {
  .modal {
    right: 0%;
  }
}
@media screen and (max-width: 644px) {
  .modal {
    top: 0;
    border-radius: 0px;
    height: 100%;
  }
}
.modal__inner {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  margin-bottom: 30px;
}
.modal__heading {
  font-size: 38px;
  font-weight: 700;
  line-height: 46px;
  background-image: linear-gradient(90.13deg, #f0145a 0.84%, #ef8080 99.96%);
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
}
@media screen and (max-width: 376px) {
  .modal__heading {
    font-size: 30px;
    line-height: 37px;
  }
}
.modal__closebtn {
  width: 32px;
  height: 32px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  align-self: flex-start;
  border: none;
}
.modal__closebtn:hover, .modal__closebtn:focus {
  border-radius: 8px;
  background: rgb(230, 230, 240);
  transition: all 0.5s;
}
.modal__form {
  display: flex;
  flex-direction: column;
}
.modal__input {
  width: 100%;
  height: 56px;
  padding: 15px;
  background: rgb(255, 255, 255);
  border: 1px solid rgb(255, 255, 255);
  border-radius: 8px;
  margin-bottom: 15px;
  color: rgb(0, 0, 0);
  font-size: 16px;
  line-height: 26px;
}
.modal__input::placeholder {
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  color: rgb(179, 179, 186);
}
.modal__input:hover, .modal__input:focus {
  border: 1px solid rgb(36, 163, 255);
  transition: all 0.5s;
}
.modal__textarea {
  width: 100%;
  height: 130px;
  resize: none;
  margin-bottom: 30px;
  padding: 15px;
  background: rgb(255, 255, 255);
  border: 1px solid rgb(255, 255, 255);
  border-radius: 8px;
  color: rgb(0, 0, 0);
  font-size: 16px;
  line-height: 26px;
}
.modal__textarea::placeholder {
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  color: rgb(179, 179, 186);
}
.modal__textarea:hover, .modal__textarea:focus {
  border: 1px solid rgb(36, 163, 255);
  transition: all 0.5s;
}
.modal__text {
  font-size: 14px;
  font-weight: 700;
  line-height: 17px;
  letter-spacing: 1px;
  color: rgb(179, 179, 186);
  text-transform: uppercase;
  margin-bottom: 15px;
}
.modal__btn {
  width: 100%;
  height: 56px;
  border-radius: 12px;
  padding: 18px 30px;
  background: rgb(36, 163, 255);
  box-shadow: 0px 0px 20px 0px rgba(36, 163, 255, 0.5);
  border: transparent;
  margin-top: 15px;
  color: rgb(255, 255, 255);
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
}
.modal__btn:hover, .modal__btn:focus {
  background: rgb(0, 133, 255);
  transition: all 0.5s;
}

.active {
  background: rgb(0, 108, 207);
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  background: rgb(50, 50, 100);
  box-shadow: 0px 15px 80px 0px rgba(40, 40, 86, 0.5);
}
.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.section-describe-service {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 280px;
}
@media screen and (max-width: 1440px) {
  .section-describe-service {
    max-width: 1440px;
    padding: 0 180px;
  }
}
@media screen and (max-width: 1024px) {
  .section-describe-service {
    max-width: 1024px;
    padding: 60px 60px 0px;
  }
}
@media screen and (max-width: 768px) {
  .section-describe-service {
    max-width: 768px;
  }
}
@media screen and (max-width: 376px) {
  .section-describe-service {
    max-width: 375px;
    padding: 30px 30px 0px;
  }
}
.section-describe-service__title {
  font-size: 38px;
  font-weight: 700;
  line-height: 46px;
  background-image: linear-gradient(90.13deg, #f0145a 0.84%, #ef8080 99.96%);
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  margin-bottom: 28px;
}
@media screen and (max-width: 376px) {
  .section-describe-service__title {
    font-size: 30px;
    line-height: 37px;
  }
}
.section-describe-service__text {
  font-size: 19px;
  line-height: 30px;
  text-align: center;
  margin-bottom: 13px;
}

.open-modal {
  max-width: 208px;
}

.section-description-service {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 60px 280px;
}
@media screen and (max-width: 1440px) {
  .section-description-service {
    max-width: 1440px;
    padding: 60px 180px;
  }
}
@media screen and (max-width: 1024px) {
  .section-description-service {
    max-width: 1024px;
    padding: 60px;
  }
}
@media screen and (max-width: 768px) {
  .section-description-service {
    max-width: 768px;
  }
}
@media screen and (max-width: 376px) {
  .section-description-service {
    max-width: 375px;
    padding: 30px 30px 60px;
  }
}
.section-description-service__line {
  width: 100%;
  height: 4px;
  background: rgba(255, 255, 255, 0.5);
  margin-bottom: 30px;
}
.section-description-service__title {
  font-size: 22px;
  font-weight: 700;
  line-height: 27px;
  margin-bottom: 30px;
}
.section-description-service__wrap {
  display: flex;
  justify-content: space-between;
  gap: 60px;
}
@media screen and (max-width: 376px) {
  .section-description-service__wrap {
    flex-direction: column;
    gap: 30px;
  }
}
.section-description-service__list {
  width: 100%;
  max-width: 650px;
}
.section-description-service__item {
  font-size: 18px;
  line-height: 26px;
  margin-bottom: 15px;
  list-style-type: disc;
}
.section-description-service__item::marker {
  color: rgb(240, 20, 90);
  font-size: 1.2em;
}
.section-description-service__item:last-child {
  margin-bottom: 0px;
}